import { Component, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { Router } from "@angular/router";
import { IOptionsPsInputNumericExposed } from "src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-numeric/ps-input-numeric.component.interfaces";
import { CommonBussinessConstant } from "src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant";
import { PsCommonBusinessSettings } from "src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings";
import { OmniPullService } from "src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service";
import { LoggerService } from "src/app/commonSRC/psServices/logger/logger.service";
import { ConstantCommon } from "src/app/commonSRC/psServices/models/common-constant";
import {
  IOptionsPsContainerPanel,
  IOptionsTemplateStepper
} from "src/app/commonSRC/psServices/models/ps-common-interface";
import { PsCommonService } from "src/app/commonSRC/psServices/ps-common/ps-common.service";
import { OmniBasePage } from "src/app/pages/omni-base/omni-base.page";

@Component({
  selector: "app-personalize-my-limit",
  templateUrl: "./personalize-my-limit.page.html",
  styleUrls: ["./personalize-my-limit.page.scss"],
})
export class PersonalizeMyLimitPage extends OmniBasePage implements OnInit {
  formGroup: FormGroup = new FormGroup({});
  myLimitVO: any = {};
  stepperOptions: IOptionsTemplateStepper = {
    stepperName: "my_limit_stepper",
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ["my_limit_step1"],
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      submitServiceUrl:
        PsCommonBusinessSettings.serviceUrl.updateUserInfoProfile,
      group: this.formGroup,
      postCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.updateLimitSession(response));
          });
        },
        executionClass: this,
        params: [this]
      }
    },
    requestObject: this.myLimitVO,
  };
  panelOptionStep1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: "my_limit_key",
    iconName: "document",
    expanded: true,
  };
  maximumLimitOptions: IOptionsPsInputNumericExposed = {
    fcName: "maxGlobalLimit",
    group: this.formGroup,
    labelKey: "maximum_daily_global_limit_key",
    placeHolder: "maximum_daily_global_limit_key",
  };
  currentLimitOptions: IOptionsPsInputNumericExposed = {
    fcName: "currentGlobalLimit",
    group: this.formGroup,
    labelKey: "current_daily_global_limit_key",
    placeHolder: "current_daily_global_limit_key",
  };
  personalizedLimitOptions: IOptionsPsInputNumericExposed = {
    fcName: "userMaximumDailyLimit",
    group: this.formGroup,
    labelKey: "my_personalized_daily_global_limit_key",
    placeHolder: "my_personalized_daily_global_limit_key",
  };
  cifInfo: any;
  dailyGlobalLimit: number = 0;
  constructor(
    public commonService: PsCommonService,
    public logger: LoggerService,
    private router: Router,
    private omniPull: OmniPullService
  ) {
    super();
  }

  ngOnInit() {
    this.cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.maximumLimitOptions.fcName, this.currentLimitOptions.fcName], 1);
  }

  ngAfterViewInit() {
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MINVALUE, [this.personalizedLimitOptions.fcName], 1);
    if (this.cifInfo.personalizationCO) {
      this.dailyGlobalLimit = this.cifInfo.personalizationCO.currentDailyGlobalLimit
      this.myLimitVO[this.maximumLimitOptions.fcName] = this.cifInfo.personalizationCO.maximumDailyGlobalLimit ? this.cifInfo.personalizationCO.maximumDailyGlobalLimit : this.dailyGlobalLimit ? this.dailyGlobalLimit : "";
      //Modified by ghada, ABEOI230155 for fixing the issue of reading the wrong value for user limit
      this.myLimitVO[this.currentLimitOptions.fcName] = this.dailyGlobalLimit > 0 ? this.dailyGlobalLimit : this.cifInfo.personalizationCO.userMaximumDailyLimit ? this.cifInfo.personalizationCO.userMaximumDailyLimit : "";
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MAXVALUE, [this.personalizedLimitOptions.fcName], this.cifInfo.personalizationCO.maximumDailyGlobalLimit ? this.cifInfo.personalizationCO.maximumDailyGlobalLimit : this.cifInfo.personalizationCO.currentDailyGlobalLimit);
    }
  }
  updateLimitSession(response?: any) {
    if (response.outputType === 'S') {
      this.omniPull.omniCommon.common.session.remove(CommonBussinessConstant.DAILY_GLOBAL_LIMIT);
      this.commonProv.setValInsideNestedObj('currentDailyGlobalLimit', this.formGroup.controls[this.personalizedLimitOptions.fcName].value, this.cifInfo.personalizationCO);
      this.omniPull.omniCommon.common.session.append(ConstantCommon.USERINFO, this.cifInfo);
    }

  }
}
